$break-tablet: 768px;
$break-desktop: 1024px;

.main{
    @media screen and (min-width: $break-desktop) {
        margin: 30px auto;
        max-width: 1024px;
    }
    
    .main-title-section{
        margin: 38px;
        @media screen and (min-width: $break-desktop) {
            margin: 0;
        }
        .main-title{
            font-size: 23px;
            font-family: 'Oswald', sans-serif;
            text-transform: uppercase;
            font-weight: 700;
            text-align: left;
        }
    }
    .main-product-info{

        .main-img{
            background-image: url('../img/mainPhoto2.png');
            height: 421px;
            background-position: center;
            background-repeat: no-repeat;
            width: 100%;
            @media screen and (min-width: $break-tablet) {
                width: 400px;
            }
        }

        .product-details{
            margin: 18px 40px 0 40px;
            .reference {
                color: #565656;
                font-size: 12px;
            }
            .prices {
                margin: 8px 0;
                display: flex;
                flex-direction: row;
                font-size: 38px;
                color: #2c303d;
                font-weight: 1000;
                .real-price{
                    margin-right: 16px;
                    .price-digit {
                        text-decoration: line-through;
                    }
                }
                .discount-price{
                    color: #ce2c52;
                    text-decoration: none;
                }
                @media screen and (min-width: $break-tablet) {
                    margin: 4px 0;
                }
                @media screen and (min-width: $break-desktop) {
                    font-size: 20px;
                    margin: 18px 0;
                }
            }
            .rating-stars{
                @media screen and (min-width: $break-tablet) {
                    margin-bottom: 22px;
                }
                @media screen and (min-width: $break-desktop) {
                    display: flex;
                }
            }
            .title-share {
                margin-left: 20px;
                color: #2c87ce;
                font-weight: 900;
                background-color: transparent;
                border: none;
                font-size: 18px;
                cursor: pointer;
            }
            .details{
                font-weight: 900;
                font-size: 14px;
            }

            .description-product{
                font-size: 15px;
                margin-bottom: 30px;
                @media screen and (min-width: $break-tablet) {
                    font-size: 13px;
                    margin-top: 2px;
                }
                @media screen and (min-width: $break-desktop) {
                    margin-bottom: 20px;
                }
            }
            .size-section{
                display: flex;
                flex-direction: column;
                font-weight: 900;
                margin-bottom: 40px;
                span {
                    @media screen and (min-width: $break-tablet) {
                        font-size: 14px;
                    }
                }
                .name-sizes{
                    margin: 18px 0 0 14px;
                    @media screen and (min-width: $break-tablet) {
                        margin: 0 48px;
                        display: flex;
                    }
    
                }
                .name-sizes span{
                    margin-right: 40px;
                    font-size: 10px;
                    font-weight: 900;

                }
                @media screen and (min-width: $break-tablet) {
                    flex-direction: row;
                    margin-bottom: 30px;
                }
                @media screen and (min-width: $break-desktop) {
                    margin-bottom: 24px;
                }
            }
            .color-section{
                font-weight: 900;
                span {
                    @media screen and (min-width: $break-tablet) {
                        font-size: 14px;
                        align-self: center;
                    }
                }
                .colors{
                    margin-top: 18px;
                    .buttons-colors{
                        display:inline-block;
                        height: 40px;
                        width: 40px;
                        border-radius: 50%;
                        border: none;
                        margin-right: 12px;
                    }
                    @media screen and (min-width: $break-tablet) {

                        margin: 0 0 0 14px;
                    }
                }
                @media screen and (min-width: $break-tablet) {
                    display: flex;
                    margin-top: 0;
                }
    
            }
            .section-button-cart{
                text-align: center;
                @media screen and (min-width: $break-tablet) {
                    text-align: left;
                }
                .button-cart{
                    height: 56px;
                    width: 280px;
                    border-radius: 100px;
                    border: none;
                    background-color: #8ae6cf;
                    margin: 44px 0;
                    font-weight: 900;
                    font-size: 18px;
                    @media screen and (min-width: $break-tablet) {
                        margin: 20px 0;
                        height: 44px;
                        width: 215px;
                    }
                    @media screen and (min-width: $break-desktop) {
                        margin-top: 16px;
                    }
                }
            }
            @media screen and (min-width: $break-tablet) {
                width: 50%;
                margin: 0 20px;
            }

        }

        @media screen and (min-width: $break-tablet) {
            display: flex;
            flex-direction: row;
            margin: 0 36px;
          }
        @media screen and (min-width: $break-desktop) {
            justify-content: space-between;
        }

    }
    .show-social{
        position: absolute;
    }  


}
.social-media-section{
    margin: 30px 0;
    display: flex;
    @media screen and (min-width: $break-tablet) {
        flex-direction: column;
        margin-left: 16px;
    }
    @media screen and (min-width: $break-desktop) {
        position: absolute;
        flex-direction: row;
        background-color: white;
        margin: 0;
        margin-top: 16px;
        padding: 10px;
    }
    .buttons-social-media{
        display:inline-block;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        font-size: 20px;
        border: none;
        text-align: center;
        margin-right: 12px;
        color: white;
        padding-top: 4px;
        @media screen and (min-width: $break-tablet) {
            margin-bottom: 10px;
        }
        @media screen and (min-width: $break-desktop) {
            margin-bottom: 0;
        }

    }
    .background-color-facebook{
        background-color: #5365d2;
    }
    .background-color-twitter{
        background-color: #57bbd4;
    }
    .background-color-tumblr{
        background-color: #2c87ce;
    }
    .background-color-youtube{
        background-color: #d72424;
    }
}


